body,
html {
  position: relative;
  overflow-x: hidden;
  font: 500 16px/1.4 "Manrope", sans-serif;
  color: #2b2b2b;
}

.container {
  max-width: 1360px;
  padding: 0 40px;
  margin: 0 auto;
  width: 100%;
}

.btn__green {
  border-radius: 4px;
  padding: 12px 24px;
  background: #23a595;
  font: 500 14px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #fff;
  width: max-content;
  transition: all 0.3s;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn__green:hover {
  background: #1c9e8e;
}
.btn__green:active {
  background: #0b8d7d;
}

.header {
  position: relative;
  overflow: hidden;
}
.header__swiper {
  overflow: visible;
}
.header__slide {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  padding: 160px 0;
}
.header__slide img,
.header__slide iframe,
.header__slide video {
  width: 100vw;
  height: 100%;
  position: absolute;
  z-index: -1;
  object-fit: cover;
  pointer-events: none;
}
.header__slide p {
  font: 700 54px/1.1 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #2b2b2b;
  max-width: 546px;
}
.header__slide .btn__green {
  margin: 32px 0 0;
}
.header .swiper-pagination {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 8px;
  max-width: 1360px;
  padding: 0 40px;
  width: 100%;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
}
.header .swiper-pagination span {
  width: 12px;
  height: 12px;
  display: block !important;
  margin: 0 !important;
  opacity: 1 !important;
  background: #f8f8f8;
  transition: all 0.3s;
}
.header .swiper-pagination span.swiper-pagination-bullet-active {
  background: #23a595;
}

.nav {
  padding: 16px 0 0;
  position: fixed;
  z-index: 100;
  width: 100%;
  transition: all 0.3s;
}
.nav__hum, .nav__soc {
  display: none;
}
.nav__logo {
  display: block;
  width: 100%;
  max-width: 186px;
  margin: 0 auto;
}
.nav__list {
  margin: 24px 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 32px;
  border-bottom: 2px solid #ebebee;
  padding: 0 0 16px;
}
.nav__list a {
  font: 500 16px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #2b2b2b;
  transition: all 0.3s;
  position: relative;
}
.nav__list a::before {
  content: "";
  display: block;
  width: 100%;
  position: absolute;
  bottom: -18px;
  left: 0;
  background: #23a595;
  height: 2px;
  opacity: 0;
  transition: all 0.3s;
}
.nav__list a:hover::before, .nav__list a.active::before {
  opacity: 1;
}
.nav.nav__fix {
  background: #fff;
}

.direct {
  padding: 120px 0 0;
  position: relative;
  overflow: hidden;
  z-index: 2;
}
.direct__tab {
  display: flex;
  padding: 0 40px;
  margin: 0 -40px;
  justify-content: start;
  align-items: center;
  overflow-x: auto;
  gap: 28px;
}
.direct__tab li {
  position: relative;
  white-space: nowrap;
  font: 600 32px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #2b2b2b;
  transition: all 0.3s;
  cursor: pointer;
}
.direct__tab li.active, .direct__tab li:hover {
  color: #23a595;
}
.direct__wrapper {
  margin: 40px 0 0;
  grid-template-columns: 470px calc(100% - 470px);
  display: none;
}
.direct__wrapper.active {
  display: grid;
}
.direct__info {
  z-index: 3;
  position: relative;
  background: #fff;
  padding-right: 10px;
}
.direct__info::before {
  content: "";
  display: block;
  width: 100vw;
  height: 100%;
  z-index: -1;
  right: 0;
  top: 0;
  position: absolute;
  background: #fff;
}
.direct__info p {
  font: 600 24px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #2b2b2b;
}
.direct__info p + p {
  font: 500 16px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #828282;
  margin: 8px 0 0;
}
.direct__info_row {
  margin: 24px 0 0;
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 12px;
}
.direct__info_link {
  display: block;
  width: 44px;
  border-radius: 4px;
  position: relative;
  overflow: hidden;
}
.direct__info_link img {
  height: 44px;
  object-fit: cover;
}
.direct__slider {
  width: 200%;
  overflow: visible;
  z-index: 1;
}
.direct__slide {
  width: 320px;
}
.direct__slide_img {
  padding: 32px;
  background: #f8f8f8;
}
.direct__slide_img img {
  width: 100%;
  height: 256px;
  object-fit: contain;
}
.direct__slide p {
  margin: 16px 0 0;
  font: 600 24px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #2b2b2b;
}

.about {
  padding: 120px 0 0;
  position: relative;
  overflow: hidden;
}
.about h2 {
  font: 600 32px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #2b2b2b;
}
.about h2 span {
  color: #23a595;
}
.about__wrapper {
  display: grid;
  grid-template-columns: 43fr 43fr 34fr;
  gap: 40px;
  align-items: start;
}
.about__har {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}
.about__har div {
  padding: 16px;
  background: #f8f8f8;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.about__har p {
  font: 700 36px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #23a595;
  margin: 0 0 98px;
}
.about__har p + p {
  font: 600 20px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #2b2b2b;
  margin: 0;
}
.about__text {
  align-self: end;
}
.about__text p {
  font: 500 16px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #828282;
  margin: 8px 0 0;
}
.about__text p:nth-child(1) {
  margin: 0;
}
.about__slider {
  overflow: visible;
  margin: 60px 0 0;
}
.about__slider .swiper-slide {
  width: 380px;
}
.about__slider img {
  width: 100%;
  height: 214px;
  object-fit: cover;
}

.perf {
  padding: 120px 0 0;
}
.perf__wrapper {
  display: none;
  grid-template-columns: 430px 1fr;
  gap: 40px;
  margin: 40px 0 0;
}
.perf__wrapper img {
  width: 100%;
}
.perf__wrapper.active {
  display: grid;
}
.perf__info p {
  font: 600 24px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #2b2b2b;
}
.perf__info p + p {
  margin: 8px 0 0;
  font: 500 16px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #828282;
}
.perf__info .btn__green {
  margin: 24px 0 0;
}
.perf__tab {
  display: flex;
  padding: 0 40px;
  margin: 0 -40px;
  justify-content: start;
  align-items: center;
  overflow-x: auto;
  gap: 28px;
}
.perf__tab li {
  position: relative;
  white-space: nowrap;
  font: 600 32px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #2b2b2b;
  transition: all 0.3s;
  cursor: pointer;
}
.perf__tab li.active, .perf__tab li:hover {
  color: #23a595;
}

.product {
  padding: 120px 0 0;
}
.product__block {
  display: grid;
  grid-template-columns: 430px 1fr;
  gap: 40px;
}
.product h2 {
  font: 600 32px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #2b2b2b;
}
.product h2 span {
  color: #23a595;
}
.product__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 12px;
}
.product__wrapper a {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 208px;
  padding: 20px;
  background: #f8f8f8;
}
.product__wrapper a img {
  width: 100%;
  max-width: 175px;
  height: 24px;
  object-fit: contain;
}

.form {
  padding: 120px 0;
}
.form__block {
  display: grid;
  grid-template-columns: 430px 1fr;
  gap: 40px;
}
.form__title h2 {
  font: 600 32px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #2b2b2b;
}
.form__title h2 span {
  color: #23a595;
}
.form__title p {
  margin: 8px 0 0;
  font: 500 16px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #828282;
  max-width: 360px;
}
.form__wrapper {
  width: calc((100% - 24px) / 3 * 2 + 12px);
  padding: 32px;
  background: #f8f8f8;
}
.form__wrapper .btn__green {
  width: 100%;
  margin: 24px 0 0;
}
.form__input p {
  font: 500 12px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #828282;
  margin: 0 0 4px 4px;
}
.form__input input {
  width: 100%;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 12px;
  background: #fff;
  font: 500 14px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #2b2b2b;
  border: none;
}
.form__input + .form__input {
  margin: 16px 0 0;
}
.form__text {
  font: 500 12px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #828282;
  margin: 12px 0 0;
}
.form__text a {
  color: #23a595;
  transition: all 0.3s;
}
.form__text a:hover {
  color: #1c9e8e;
}

.footer {
  padding: 40px 0;
  background: #2b2b2b;
}
.footer__block {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  gap: 12px;
  justify-content: space-between;
}
.footer__logo {
  display: block;
  max-width: 140px;
}
.footer__logo img {
  width: 100%;
}
.footer__soc {
  margin: 16px 0 0;
  display: flex;
  justify-content: start;
  gap: 12px;
}
.footer__soc a {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #828282;
  width: 28px;
  height: 28px;
  border-radius: 100%;
}
.footer__list p {
  font: 500 16px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #fff;
}
.footer__list ul {
  margin: 12px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
}
.footer__list ul a {
  font: 500 14px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #828282;
  transition: all 0.3s;
}
.footer__list ul a:hover {
  color: #fff;
}
.footer__map {
  width: 417px;
}
.footer__map_text {
  margin: 8px 0 0;
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: 16px 1fr;
  gap: 4px;
}
.footer__map_text p {
  font: 500 12px/1.4 "Manrope", sans-serif;
  letter-spacing: 0.02em;
  color: #fff;
}

.modal-thanks {
  position: fixed;
  top: 126px;
  right: 16px;
  border: 1px solid #ebebee;
  border-radius: 16px;
  padding: 20px 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f8f8f8;
  z-index: 100;
  gap: 12px;
  transform: translateX(120%);
  transition: all 0.3s;
  max-width: calc(100% - 32px);
}
.modal-thanks__close {
  margin: 0 0 0 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.modal-thanks.active {
  transform: none;
}

@media (max-width: 1200px) {
  .footer__block {
    grid-template-columns: auto auto auto auto;
  }
  .footer__map {
    grid-column: 1/5;
    width: 100%;
  }
  .footer__map iframe {
    height: 535px;
  }
}
@media (max-width: 1024px) {
  .nav__list {
    gap: 20px;
  }
  .about {
    padding: 80px 0 0;
  }
  .about__wrapper {
    grid-template-columns: 1fr 1fr;
    row-gap: 24px;
  }
  .about__text {
    grid-column: 2/3;
  }
  .product {
    padding: 80px 0 0;
  }
  .product__wrapper, .product__block {
    grid-template-columns: 1fr 1fr;
  }
  .form {
    padding: 80px 0;
  }
  .form__block {
    grid-template-columns: 1fr 1fr;
  }
  .form__wrapper {
    width: 100%;
  }
  .direct {
    padding: 80px 0 0;
  }
  .direct__wrapper {
    grid-template-columns: calc(50% + 40px) calc(50% - 40px);
  }
  .direct__info {
    padding: 0 40px 0 0;
  }
  .direct__slider {
    width: 100%;
  }
  .perf {
    padding: 80px 0 0;
  }
  .perf__wrapper {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 768px) {
  body {
    overflow: visible;
  }
  .btn__green {
    padding: 10px 20px;
    font-size: 13px;
  }
  .nav {
    position: sticky;
    top: 0;
    padding: 0;
    background: #fff;
  }
  .nav__list {
    display: none;
  }
  .nav__row {
    padding: 14px 0;
    display: flex;
    align-items: center;
    position: relative;
  }
  .nav__row::before {
    content: "";
    display: block;
    width: 100vw;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    background: #ebebee;
    position: absolute;
    height: 1px;
  }
  .nav__logo {
    margin: 0 auto 0 0;
  }
  .nav__hum {
    display: block;
    cursor: pointer;
    position: relative;
    width: 20px;
    height: 14px;
  }
  .nav__hum::before, .nav__hum::after,
  .nav__hum span {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: #2b2b2b;
  }
  .nav__hum::before {
    top: 100%;
  }
  .nav__hum span {
    top: 50%;
  }
  .nav.active {
    position: fixed;
    height: 100vh;
    padding: 0 0 40px;
    overflow-y: auto;
  }
  .nav.active .nav__hum span {
    opacity: 0;
  }
  .nav.active .nav__hum::before {
    top: 50%;
    transform: rotate(45deg);
  }
  .nav.active .nav__hum::after {
    top: 50%;
    transform: rotate(-45deg);
  }
  .nav.active .nav__list {
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
    border-bottom: none;
    padding: 0;
    margin: 32px 0 0;
  }
  .nav.active .nav__list a {
    font-size: 24px;
  }
  .nav.active .nav__list a::before {
    content: none;
  }
  .nav.active .nav__soc {
    display: flex;
    justify-content: start;
    align-items: center;
    gap: 12px;
    margin: 40px 0 0;
  }
  .nav.active .nav__soc a {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #23a595;
    border-radius: 100%;
  }
  .header__slide {
    padding: 120px 0;
    min-height: auto;
  }
  .header__slide p {
    font-size: 32px;
  }
  .header__slide .btn__green {
    margin: 24px 0 0;
  }
  .header .swiper-pagination {
    justify-content: center;
    bottom: 12px;
  }
  .header .swiper-pagination span {
    width: 8px;
    height: 8px;
  }
  .direct__tab {
    gap: 20px;
  }
  .direct__tab li {
    font-size: 24px;
  }
  .direct__wrapper {
    margin: 29px 0 0;
    grid-template-columns: 100%;
    gap: 28px;
  }
  .direct__info {
    padding: 0;
    max-width: 470px;
  }
  .direct__info p {
    font-size: 22px;
  }
  .direct__info p + p {
    font-size: 15px;
  }
  .direct__slide {
    width: 240px;
  }
  .direct__slide p {
    font-size: 22px;
  }
  .direct__slide_img {
    padding: 32px;
  }
  .direct__slide_img img {
    height: 176px;
  }
  .about__wrapper {
    grid-template-columns: 1fr;
    gap: 28px;
  }
  .about__text {
    grid-column: auto;
  }
  .about__text p {
    font-size: 15px;
  }
  .about__har {
    margin: 4px 0 0;
  }
  .about__har p {
    font-size: 28px;
    margin: 0 0 auto;
  }
  .about__har p + p {
    margin: 32px 0 0;
    font-size: 18px;
  }
  .about h2 {
    font-size: 24px;
  }
  .about__slider {
    margin: 28px 0 0;
  }
  .about__slider .swiper-slide {
    width: calc(100vw - 80px);
  }
  .about__slider .swiper-slide img {
    height: 415px;
  }
  .perf__wrapper {
    grid-template-columns: 1fr;
    gap: 28px;
    margin: 28px 0 0;
  }
  .perf__tab {
    gap: 20px;
  }
  .perf__tab li {
    font-size: 24px;
  }
  .perf__info p {
    font-size: 22px;
  }
  .perf__info p + p {
    font-size: 15px;
  }
  .product__block {
    grid-template-columns: 1fr;
    gap: 32px;
  }
  .product h2 {
    font-size: 24px;
    max-width: 343px;
  }
  .product__wrapper a {
    height: 134px;
  }
  .product__wrapper img {
    max-width: 145px;
    height: 20px;
  }
  .form__block {
    grid-template-columns: 1fr;
    gap: 32px;
  }
  .form__title h2 {
    font-size: 24px;
  }
  .form__title p {
    font-size: 15px;
    max-width: 340px;
  }
  .footer__block {
    grid-template-columns: 1fr;
    gap: 32px;
  }
  .footer__map {
    grid-column: auto;
  }
  .footer__map iframe {
    height: 193px;
  }
}
@media (max-width: 600px) {
  .container {
    padding: 0 16px;
  }
  .about__slider .swiper-slide {
    width: calc(100vw - 32px);
  }
  .about__slider .swiper-slide img {
    height: 193px;
  }
  .form__wrapper {
    padding: 20px;
  }
}